<template>
  <div id="normal-box">
    <div class="top-title">温馨提示：设置默认保洁项目后，保洁人员在查房或打扫时，会自动勾选默认的项目，提升保洁速度。</div>
    <van-tabs
      v-model="selected"
      title-active-color='#ff7d41'
      color='#ff7d41'
    >
      <van-tab title="消耗品">
      </van-tab>
      <van-tab title="洗涤品">
      </van-tab>
    </van-tabs>
    <div class='scroll'>
      <div
        v-for="(item,i) in leaningAll"
        :key="i"
        class="cleaning-center"
      >
        <div>
          <div>{{ item.costName }}</div>
          <div class="b2">{{ item.unit }}</div>
        </div>
        <van-stepper
          v-model="item.num"
          min='0'
          theme="round"
          button-size="22"
        />
      </div>
    </div>
    <van-button
      v-if="res_order.length != 0"
      class="bottom-btn"
      @click="commit"
    >保存设置</van-button>
  </div>
</template>
<script>
import { GetBusinessHotelCostTemplateByroomscale, PostBusinessHotelCostTemplateAdd, PostBusinessHotelCostTemplateEdit } from '@/api/api_cleaning_or_goods';
import { Toast } from 'vant';
export default {
  data() {
    return {
      selected: '0',
      res_order: [],
      leaningAll: []
    };
  },
  watch: {
    selected(neww, olld) {
      this.hotelCostType = neww == '0' ? '2' : '3'
      this.getInfo()
    }
  },
  created() {
    this.$store.commit('setHeader', '默认保洁项目');
  },
  methods: {
    getInfo() {
      this.leaningAll = []
      let data = {
        hotelId: this.$route.query.hotelId,
        roomScaleId: this.$route.query.roomScaleId,
        hotelCostType: this.hotelCostType || '2'
      }
      GetBusinessHotelCostTemplateByroomscale(data).done(res => {
        this.res_order = res.order
        if (this.res_order.length == 0) {
          Toast(`暂无默认${data.hotelCostType == '2' ? '消耗品' : '洗涤品'}项目`)
          return
        } else {
          this.res_data = res.data   // 有无模板
          if (res.data) {
            this.res_data_costInfo = JSON.parse(res.data.costInfo)
            this.leaningAll = this.res_order.map((item, i) => {
              return { ...item, ...this.res_data_costInfo[i] }
            })
          } else {
            this.leaningAll = this.res_order.map(item => {
              item.num = '0'
              return item
            })
          }
        }
      })
    },
    // 保存设置
    commit() {
      let arr = []
      this.leaningAll.map(item => {
        let obj = {}
        obj.id = item.id
        obj.num = item.num
        arr.push(obj)
      })
      if (!this.res_data) {
        // 新增
        let dataForm = {
          hotelId: this.$route.query.hotelId,
          roomScaleId: this.$route.query.roomScaleId,
          hotelCostType: this.hotelCostType || '2',
          costInfo: JSON.stringify(arr),
        }
        PostBusinessHotelCostTemplateAdd(dataForm).done(res => {
          Toast({
            message: res.msg,
            duration: 900,
          })
          setTimeout('window.history.go(-1)', 900);
        })
      } else {
        // 编辑
        let dataForm = {
          id: this.res_data.id,
          costInfo: JSON.stringify(arr),
        }
        PostBusinessHotelCostTemplateEdit(dataForm).done(res => {
          Toast({
            message: res.msg,
            duration: 900,
          })
          setTimeout('window.history.go(-1)', 900);
        })
      }

    }
  }
};
</script>
<style scoped>
#normal-box {
    font-size: 14px;
    color: #000;
}
.cleaning-center {
    display: flex;
    justify-content: space-between;
    padding: 10px 20px;
    border-bottom: 1px solid #f8f6f9;
    background: #fff;
    display: flex;
    align-items: center;
    text-align: left;
}
.top-title {
    padding: 4px 8px;
    background: #f6e5dc;
    color: #595959;
    margin-bottom: 8px;
    text-align: left;
}
.bottom-btn {
    background: #ff7d41;
    color: #fff;
    width: 100%;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
}
.b2 {
    color: #b2b2b2;
}
.scroll {
    margin-top: 8px;
    position: fixed;
    left: 0;
    top: 150px;
    right: 0;
    overflow: scroll;
    height: 68%;
}
/deep/ .van-stepper--round .van-stepper__minus {
    background: #b2b2b2;
    color: #fff;
    border: 1px solid #b2b2b2;
}
/deep/ .van-stepper--round .van-stepper__plus {
    background: #ff7d41;
}
/deep/ .van-stepper__input {
    font-weight: bold;
}
</style>



